// INDEX
// ===============


// MAIN TITLE
// ===============
.main__title {
  margin-bottom: 15px;
  text-align: center;
  font-weight: 800;
  @include rmin($mobile-s) {
    font-size: 30px; }
  @include rmin($mobile) {
    font-size: 35px; }
  @include rmin($tablet) {
    font-size: 40px; }
  @include rmin($wide) {
    font-size: 50px; } }

// FORM COMPONENT
// ===============
.form {
  &__input,
  &__message {
    border-radius: 2em;
    border: 1px solid rgba($colorVeryLightGrey, 0.5);
    background-color: $colorWhite;
    color: $colorNero;
    font-size: 14px;
    font-family: 'TTNorms', Arial, sans-serif;
    @include transition(border-color, box-shadow);
    &:focus {
      border-color: $colorGold;
      box-shadow: 0 0 20px 0 rgba($colorBlack, 0.03);
      @include transition(border-color, box-shadow); } }

  &__field {
    &-group {
      display: flex;
      justify-content: space-between;
      margin-top: 30px;
      @include r(767) {
        flex-direction: column;
        margin-top: 15px; }

      input[type="file"] {
        opacity: 0;
        z-index: -1;
        overflow: hidden;
        position: absolute;
        width: 0.1px;
        height: 0.1px;

        + label {
          display: inline-block;

          p {
            width: 203px;
            text-align: center;
            border-color: #999;
            background-color: #999;
            color: #fff;
            text-transform: uppercase;
            @include transition(color, background-color);

            &:hover {
              color: #999;
              background-color: transparent;
              @include transition(color, background-color); } }

          span {
            display: block;
            color: $colorBlack;
            font-weight: 300;
            margin-top: 5px;
            text-decoration: underline; } } } }

    .form__button {
      width: 170px;
      margin: 0;
      @include r(767) {
        margin-top: 15px; } }

    &-select {
      display: flex;
      align-items: center;

      label {
        width: 85px;
        font-size: 20px; }

      .select {
        width: calc(100% - 85px);

        .selectric {
          span {
            text-align: left; } } } } }

  &__input {
    width: 100%;
    height: 45px;
    padding-left: 20px;
    padding-right: 20px; }

  &__message {
    width: 100%;
    height: 170px;
    resize: none;
    padding: 20px; }

  &__button {
    cursor: pointer;
    width: 100%;
    max-width: 260px;
    height: 45px;
    line-height: 40px;
    padding-left: 15px;
    padding-right: 15px;
    border: 2px solid $colorGold;
    border-radius: 2em;
    background-color: $colorGold;
    font-size: 15px;
    font-weight: 500;
    @include transition(background-color);
    &:hover {
      background-color: transparent;
      @include transition(background-color); } } }

// SELECT
// ===============
.select {
  cursor: pointer;

  .selectric {
    overflow: hidden;
    min-width: 185px;
    height: 45px;
    padding-left: 20px;
    padding-right: 50px;
    border: none;
    border-radius: 2em;
    background-color: $colorSolitude;

    > span {
      height: 45px;
      line-height: 45px;
      margin: 0;
      padding-left: 30px;
      text-align: center;
      @include rmin($mobile-s) {
        font-size: 18px; }
      @include rmin($mobile) {
        font-size: 20px; } }

    > b {
      height: 45px;
      line-height: 45px;
      background-color: $colorSolitude;
      font-size: 0;
      &:before {
        content: "";
        position: absolute;
        top: 50%;
        right: 12px;
        transform: translateY(-50%);
        @include sprite(arrow); }
      &:after {
        display: none; } } }

  .selectric-items {
    background-color: $colorSolitude;

    li {
      @include rmin($mobile-s) {
        font-size: 14px; }
      @include rmin($mobile) {
        font-size: 16px; } } } }

// RADIO
// ===============
.radio {
  cursor: pointer;
  position: relative;

  input[type="radio"] {
    position: absolute;
    width: 0;
    height: 0;
    opacity: 0;
    border: none;
    margin: 0;
    padding: 0;
    filter: alpha(opacity=0);
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
    outline: none; }

  label {
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 45px;
    height: 45px;
    border-radius: 100%;
    background-color: $colorSolitude;
    @include transition(background-color, color);

    span {
      font-size: 20px; } }

  &.is-active {
    label {
      color: $colorWhite;
      background-color: $colorIrisBlue;
      @include transition(background-color, color); } } }

// CHECKBOX
// ===============
.checkbox {
  position: relative;
  cursor: pointer;

  input[type="checkbox"] {
    position: absolute;
    width: 0;
    height: 0;
    opacity: 0;
    border: none;
    margin: 0;
    padding: 0;
    filter: alpha(opacity=0);
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
    outline: none; }

  label {
    cursor: pointer;
    display: flex;
    align-items: center;
    margin-bottom: 15px; }

  &__icon {
    position: relative;
    width: 33px;
    margin-right: 12px;

    span {
      display: block;
      width: 100%;
      height: 15px;
      border-radius: 2em;
      background-color: $colorSolitude;
      &:before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 15px;
        height: 15px;
        border-radius: 100%;
        background-color: $colorDimGray;
        @include transition(background-color, left);

        .is-active & {
          left: calc(100% - 12px); }

        .is-active.checkbox--irisBlue & {
          background-color: $colorIrisBlue;
          @include transition(background-color, left); }
        .is-active.checkbox--deepCerise & {
          background-color: $colorDeepCerise;
          @include transition(background-color, left); }
        .is-active.checkbox--darkTangerine & {
          background-color: $colorDarkTangerine;
          @include transition(background-color, left); }
        .is-active.checkbox--fern & {
          background-color: $colorFern;
          @include transition(background-color, left); }
        .is-active.checkbox--violetBlue & {
          background-color: $colorVioletBlue;
          @include transition(background-color, left); } } } }

  &__name {
    font-size: 14px; } }

